body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.media-snippet {
    padding: 1.5rem;
}

.media {
    align-items: flex-start;
    display: flex;
    text-align: left;
}

figure {
    display: block;
    margin: 0;
    padding: 0;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 40px;
            margin-inline-start: 40px;
    -webkit-margin-end: 40px;
            margin-inline-end: 40px;
}

.media-left {
    margin-right: 1rem;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
    margin-left: 0px;
    margin-top: 0px;
}

.media-content {
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 1;
    text-align: left;
}

.media .content:not(:last-child) {
    margin-bottom: 0.75rem;
}

.media-right {
    margin-left: 1rem;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
}

.image {
    display: block;
    position: relative;
}

p {
    display: block;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}

.level.is-mobile {
    display: flex;
}

.level-left {
    align-items: center;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
}

.level-item {
    align-items: center;
    display: flex;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
    justify-content: center;
}
.search-button {
    background-color: #fff;
    color: #845007;
    border: 1px solid #dbdbdb;
}
  .navbar-light {
    background-color: #c9af98;
    height: 34px;
    line-height: 34px;
    letter-spacing: 0.1em;
  }
  
.has-background-tan {
    background-color: #c9af98;
  }
.brand-title {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.18em;
  font-size: 1.25rem;
}

.heading-title {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.18em;
  font-size: 0.9rem;
}

.heading-subtitle {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.25em;
  font-size: 0.8rem;
}

p,
.p-article {
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 0.75rem;
}

.hero-is-fullheight {
  min-height: 100vh;
}
.hero-head {
  flex-grow: 0;
  flex-shrink: 0;
}
.hero-body {
  align-items: center;
  display: flex;
}
.hero-foot {
  flex-grow: 0;
  flex-shrink: 0;
}

.col-centered {
  float: none;
  margin: 0 auto;
}

.card-content {
  padding: 1.5rem;
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  color: #4a4a4a;
  max-width: 100%;
  position: relative;
}

.save-button,
.delete-button,
.view-button {
  background-color: #fff;
  color: #845007;
  cursor: pointer;
  justify-content: center;
  padding-bottom: calc(0.375em - 1px);
  padding-left: 0.75em;
  padding-right: 0.75em;
  padding-top: calc(0.375em - 1px);
  text-align: center;
  white-space: nowrap;
  position: relative;
  vertical-align: top;
  line-height: 1.5;
  align-items: center;
  border: 1px solid #dbdbdb;
  border-radius: 4px;
  box-shadow: none;
  display: inline-flex;
  font-size: 1rem;
  height: 2.25em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-decoration: none;
}

.image-250 {
  margin-left: auto;
  margin-right: auto;
  width: 250px;
  height: auto;
}

.image.is-128xauto {
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: auto;
}

.mockup-content {
  height: 370px;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  position: relative;
  display: block;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.125);
}

